import * as React from 'react'
import { Link } from 'gatsby'
import * as styles from '../styles/cards.module.css'

const Cards = ({images}) => {
  return (
    <div className={styles.container}>
      {images.map(src => (
        <Link to={src[1]} target="_blank">
          <img loading="lazy" src={src[0]} alt=""/>
        </Link>
      ))}
    </div>
  )
}

export default Cards